/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

section#blog-template {
	display: flex;
	max-width: unset !important;
	flex-wrap: nowrap;
	gap: 64px;

	@media (width < 999px) {
		flex-wrap: wrap;
	}

	.wrapper {
		background: #FFF;
		box-shadow: -219px 0px 61px 0px rgba(221, 246, 254, 0.00), -140px 0px 56px 0px rgba(221, 246, 254, 0.01), -79px 0px 47px 0px rgba(221, 246, 254, 0.05), -35px 0px 35px 0px rgba(221, 246, 254, 0.09);
		gap: 24px;
		padding: 32px;
		display: grid;
		position: relative;
		/* Optional: Can be removed if unnecessary */
		border-radius: 32px;
		/* Remove align-self to let height adjust dynamically */
		align-self: flex-start;
		/* Or flex-start if needed for alignment */
		/* Ensure height is not restricted */
		height: auto;
		min-height: auto;
		/* In case min-height was set */
		@media (width < 999px) {
			justify-items: center;
		}
	}

	.toc-container {
		max-width: 450px;
		display: flex;
		flex-direction: column;
		gap: 48px;

		@media (width < 999px) {
			max-width: unset;
			width:100vw;
			margin: auto;
		}

		.post-meta {
			display: flex;
			align-items: flex-start;
			border-radius: 32px;
			gap: 12px;

			>* {
				color: #1A4553;
				text-align: center;
				font-size: 12px;
				font-style: normal;
				font-weight: 400;
			}

			.post-date::after {
				content: '\2022';
				margin-left: 12px;
			}
		}

		.post-title {
			color: #1A4553;
			font-size: 20px;
			font-style: normal;
			font-weight: 400;
		}

		.featured-image {
			border-radius: 24px;
			width: 100%;
			height: auto;
			max-width:450px;
		}

		.selected-categories {
			display: flex;
			gap: 12px;
			flex-wrap: wrap;
			align-items: center;

			.tag {
				color: #1A4553;
				font-size: 13px;
				font-weight: 400;
				position: relative;
				padding: 8px 16px 8px 14px;
				border-radius: 500px;
				border: 1px solid #B9DAE5;
				background: #F1FCFF;
				gap: 6px;
				align-items: center;
				display: flex;
				margin: unset !important;
			}
		}
	}

	ul {
		list-style: none !important;
		padding: 0;

		li {
			display: flex;
			/* Align the icon and text */
			margin-bottom: 0.5rem;
		}

