	.table-of-contents {
		height: 100%;

		.contents-wrapper {
			display: flex;
			flex-direction: column;
			gap: 24px;
			padding: 32px;
			border-radius: 32px;
			background: #FFF;
			box-shadow: -219px 0px 61px 0px #DDF6FE, -140px 0px 56px 0px rgba(221, 246, 254, 0.01), -79px 0px 47px 0px rgba(221, 246, 254, 0.05), -35px 0px 35px 0px rgba(221, 246, 254, 0.09);
			max-height: 50vh;
			overflow-y: auto;
			top: 50px;
			position: sticky;
			align-self: flex-start;

			h2 {
				color: #1A4553;
				font-size: 32px;
				font-style: normal;
				font-weight: 500;
			}

			a {
				color: #1A4553;
				font-size: 16px;
				font-style: normal;
				font-weight: 400;
				text-decoration-line: underline;
				text-decoration-style: solid;
				text-underline-offset: auto;

			}

			h2,
			a {
				padding: unset !important;
				margin: unset !important
			}
		}
	}

	.toc-link {
		width: fit-content !important;
	}
}